/* src/styles.css */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
}

.header {
  background-color: #333;
  color: #fff;
  padding: 1rem;
  text-align: center;
}

.header nav a {
  color: #fff;
  margin: 0 1rem;
  text-decoration: none;
}

.header nav a:hover {
  text-decoration: underline;
}

.main {
  padding: 2rem;
}

.footer {
  background-color: #333;
  color: #fff;
  padding: 1rem;
  text-align: center;
  position: absolute;
  width: 100%;
  bottom: 0;
}
